<template>
  <div class="footer shadow h_max grid grid_vertical_center">
    <div class="nav w_max flex flex_end">
      <div class="button_group">
        <button class="btn nav_btn btn_radius icon-edit" @click="edit"></button>
        <button class="btn nav_btn btn_radius icon-delete" @click="showWindow"></button>
      </div>
    </div>
    <modal :options="options" @down="showWindow">
      <warning :options="warning" @cancel="showWindow" @confirm="confirm"></warning>
    </modal>
  </div>
</template>

<script>
import modal from '@/components/modal.vue'
import warning from '@/components/warning.vue'
export default {
  components: {
    modal,
    warning
  },
  props: [
    'id'
  ],
  data () {
    return {
      options: {
        show: false
      },
      warning: {
        message: 'Данная заметка будет удалена навсегда!'
      }
    }
  },
  methods: {
    showWindow () {
      this.options.show = !this.options.show
    },
    confirm () {
      this.$store.dispatch('DELETE_NOTE', this.id)
    },
    edit () {
      this.$router.push(`/edit/${this.id}`)
    }
  }
}
</script>

