<script>
import todoHeader from '@/components/header'
import preloader from '@/components/preloader'
export default {
  components: {
    'todo-header': todoHeader,
    preloader
  },
  data () {
    return {
      loading: true
    }
  },
  computed: {
    loader () {
      return this.loading
    }
  },
  created () {
    setTimeout(item => {
      this.loading = false
    }, 3000)
  }
}
</script>

