import Vue from 'vue'
import App from './App.vue'
import './style/main.scss'

import router from './router'
import store from './store'

import '@/plugins/firebase'

Vue.config.productionTip = false

new Vue({
  router,
  store,
  beforeCreate () {
    this.$store.dispatch('FIREBASE_INIT')
  },
  render: h => h(App)
}).$mount('#app')
