<template>
  <div class="warning grid h_max">
    <div class="warning__body grid grid_vertical_center grid_center">
      <h3 class="warning__message">{{ message }}</h3>
    </div>
    <div class="warning__footer flex flex_end">
      <div class="button_group">
        <button class="btn btn_big btn_warning" @click="confirm">ОК</button>
        <button class="btn btn_big btn_dark" @click="cancel">Отмена</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: [
    'options'
  ],
  data () {
    return {
      message: this.options.message
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel', true)
    },
    confirm () {
      this.$emit('confirm', true)
      this.cancel()
    }
  }
}
</script>

<style lang="scss" scoped>
.warning {
  grid-template-rows: 1fr auto;
  padding: 1rem;
}
.warning__message {
  font-size: 2rem;
  font-weight: lighter;
  color: rgba(0, 0, 0, 0.4);
}
</style>
