import firebase from 'firebase/app'
export default {
  state: {
    list: []
  },
  mutations: {
    setList: (state, payload) => {
      state.list = payload
    },
    deleteNote: (state, payload) => {
      state.list.splice(payload, 1)
    }
  },
  actions: {
    /**
     * Удаляет Заметку
     */
    async DELETE_NOTE ({ commit }, payload) {
      commit('clearError')
      try {
        // firebase.database().ref('/note').push(payload)
        firebase.database().ref(`/note/${payload}`).remove()
      } catch (error) {
        commit('setError', error.message)
        throw error
      }
    },
    /**
     * Добавляет заметку
     */
    async ADD_NOTE ({ commit }, payload) {
      commit('clearError')
      try {
        firebase.database().ref('/note').push(payload)
      } catch (error) {
        commit('setError', error.message)
        throw error
      }
    },
    async UPDATE_NOTE ({ commit }, payload) {
      commit('clearError')
      try {
        // firebase.database().ref('/note').push(payload)
        firebase.database().ref(`/note/${payload.id}`).update(payload.note)
      } catch (error) {
        commit('setError', error.message)
        throw error
      }
    }
  },
  getters: {
    getList: (state) => state.list
  }
}
