<template>
  <div class="body">
    <ul class="list">
      <li class="item grid grid_vertical_center" v-for="(item, index) in minList" :key="index">
        <span class="item__text">{{ item.text }}</span>
        <span v-if="item.status" class="item__status icon-check"></span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: [
    'content'
  ],
  data () {
    return {
      list: this.content
    }
  },
  computed: {
    minList () {
      return this.list.slice(0, 3)
    }
  }
}
</script>

