import firebase from 'firebase/app'
import firebaseConfig from '@/config/firebase'

export default {
  state: {},
  mutations: {},
  actions: {
    async FIREBASE_INIT ({ commit, dispatch }) {
      commit('clearError')
      try {
        await firebase.initializeApp(firebaseConfig)
        dispatch('LOAD_NOTE')
      } catch (error) {
        commit('setError', error.message)
        throw error
      }
    },
    async LOAD_NOTE ({ commit }) {
      commit('clearError')
      try {
        const list = await firebase.database().ref('/note')
        list.on('value', snapshot => {
          commit('setList', snapshot.val())
        })
      } catch (error) {
        commit('setError', error.message)
        throw error
      }
    }
  },
  getters: {}
}
