<template>
  <transition name="modal">
    <div v-if="open" @click="down($event)" class="modal-space grid grid_vertical_center grid_center">
      <div class="window grid"><slot></slot></div>
    </div>
  </transition>
</template>

<script>
export default {
  props: [
    'options'
  ],
  data () {
    return {}
  },
  computed: {
    open () {
      return this.options.show
    }
  },
  methods: {
    down (event) {
      if (event.target.classList.contains('modal-space')) this.$emit('down', true)
    }
  }
}
</script>

<style lang="scss" scoped>
.modal-space {
  z-index: 100;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.window {
  max-width: 70vw;
  width: 100%;
  min-height: 70vh;
  background-color: white;
  box-shadow: 0 0 30px rgba(95, 95, 95, 0.5);
}
.modal-enter-active, .modal-leave-active {
  transition: opacity .3s;
}
.modal-enter, .modal-leave-to {
  opacity: 0;
}
</style>
